.my-navbar {
  background-color: #112;
  display: flex;
  justify-content: space-evenly;
  font-size: 24px;
  padding: 15px;
  height: 100%;
}

.my-navbar a {
  color: steelblue;
  outline: none;
}

.my-navbar a:hover {
  transition: 0.3s;
  text-decoration: none;
  color: lightskyblue;
}

.active {
  color: lightskyblue !important;
  border-bottom: 2px lightskyblue solid;
}

@media screen and (max-width: 600px) {
  .my-navbar {
    height: 100%;
  }
}

@media screen and (max-width: 1024px) {
  .my-navbar {
    height: 100%;
  }
}
